body{
  background-color: #6690a5!important;
  color:#ffffff!important;
}
#root{
  width:560px;
  margin:5px auto auto auto;
  background-color:black;
  border: 1px solid black;
}
.bg-primary, button{
  background-color:#263238 !important;
  border-color:transparent !important;
  outline: transparent;
}
.btn:focus, .btn-focus{outline:none!important; box-shadow: 0 0 0 rgba(0, 123, 255, 0)!important;}
.bg-secondary{
  background-color:#263238 !important;
}
.table-bordered {
   border:none;
}


@media screen and (prefers-color-scheme:dark){
  #root{
    background-color:black;
    color:white;
  }
}